import { Component } from '@angular/core';

import { NavController } from 'ionic-angular';
import {Page1Page} from '../page1/page1';
import {Page2Page} from '../page2/page2';
import {Page3Page} from '../page3/page3';
import {Page4Page} from '../page4/page4';
import {Page5Page} from '../page5/page5';
import {Page6Page} from '../page6/page6';
import { HttpClient  } from '@angular/common/http';
import {Observable} from 'rxjs/Observable';


@Component({

   selector: 'page-home',
  
   templateUrl: 'home.html'

})

export class HomePage {



  public items:any;
  constructor(public navCtrl: NavController,public http: HttpClient) {
    this.loadData();
  } 

loadData(){
let data:Observable<any>;
data = this.http.get('http://jsonplaceholder.typicode.com/posts');
data.subscribe(result => {
  this.items = result;
})
}

	FirstPage(ket){
		this.navCtrl.push("Page1Page",{kategori:ket});
	}
	
	SecondPage(ket){
		this.navCtrl.push("Page2Page",{kategori:ket});
	}

	ThirdPage(ket){
		this.navCtrl.push("Page3Page",{kategori:ket});
	}

	FourthPage(ket){
		this.navCtrl.push("Page4Page",{kategori:ket});
	}

	FifthPage(ket){
		this.navCtrl.push("Page5Page",{kategori:ket});
	}

	SixthPage(ket){
		this.navCtrl.push("Page6Page",{kategori:ket});
	}
	itemClick(itemid:class){
		this.navCtrl.push("Page1Page",{itemid});
		}
	}


